export default {
  data: function data() {
    return {
      Bonusloading: false,
      BonusList: [],
      BonusVisible: false,
      inviteImage1: require("../../assets/Invite1.jpg"),
      inviteImage2: require("../../assets/Invite2.jpg"),
      inviteDialog: {
        visible: false,
        inviteContents: "",
        inviteCode: ""
      },
      BonusDialog: {
        id: '',
        page: 1,
        pageSizes: [10, 20, 50, 100],
        rowsPerPage: 10,
        items: [],
        total: 0,
        totalPage: 0
      },
      newEditDialog: {
        isUnfold: true,
        visible: false,
        loading: false,
        name: "",
        shopUrl: "",
        remark: "",
        email: "",
        id: "",
        weChat: "",
        skype: "",
        line: "",
        QQ: "",
        wangwang: "",
        whatsapp: "",
        vendorId: null
      },
      newAddVendorDialog: {
        visible: false,
        content: "",
        btnNextLoading: true,
        radio: 1,
        ourmallId: "",
        name: ""
      },
      loading: false,
      tableHeight: 400,
      reSizeTime: 0,
      pageSizes: [10, 20, 50, 100],
      page: this.$route.query.page ? Number(this.$route.query.page) : 1,
      rowsPerPage: localStorage.getItem("c_myVendorPerPage") ? Number(localStorage.getItem("c_myVendorPerPage")) : 10,
      items: [],
      total: 0,
      totalPage: 0,
      filterParams: {
        vendorId: "",
        vendorLike: "",
        isRegister: "",
        productId: "",
        productName: ""
      },
      filterParamsDefault: "{}",
      addVendorDialog: {
        isShow: false,
        loading: false,
        labelW: "120px",
        name: "",
        remark: "",
        email: "",
        id: "",
        weChat: "",
        skype: "",
        line: "",
        QQ: "",
        wangwang: "",
        whatsapp: ""
      },
      addVendorDialogDefault: "{}"
    };
  },
  components: {},
  watch: {
    $route: "gotoPage"
  },
  computed: {},
  mounted: function mounted() {
    this.addVendorDialogDefault = JSON.stringify(this.addVendorDialog);
    this.filterParamsDefault = JSON.stringify(this.filterParams);
    this.getItem(); // window.onresize = () => {
    //   this.$emit("getTableHeight", this);
    // };
  },
  beforeDestroy: function beforeDestroy() {// window.onresize = () => {};
  },
  methods: {
    changeBonusVisiblePageSize: function changeBonusVisiblePageSize(val) {
      this.BonusDialog.rowsPerPage = val;
      this.getBonus();
    },
    toBonusVisiblePage: function toBonusVisiblePage(val) {
      this.BonusDialog.page = val;
      this.getBonus();
    },
    closeBonusVisible: function closeBonusVisible() {
      this.BonusDialog = {
        id: '',
        page: 1,
        pageSizes: [10, 20, 50, 100],
        rowsPerPage: 10,
        items: [],
        total: 0,
        totalPage: 0
      }, this.BonusVisible = false;
      this.BonusList = [];
    },
    openBonusDetail: function openBonusDetail(id) {
      this.BonusDialog.id = id;
      this.getBonus();
      this.BonusVisible = true;
    },
    getBonus: function getBonus() {
      var _this = this;

      this.Bonusloading = true;
      this.$apiCall("api.Relationship.bonusList", {
        relationshipId: this.BonusDialog.id,
        page: this.BonusDialog.page,
        rowsPerPage: this.BonusDialog.rowsPerPage
      }, function (r) {
        _this.Bonusloading = false;

        if (r.ErrorCode == 9999) {
          _this.BonusList = r.Data.Results;
          _this.BonusDialog.total = Number(r.Data.Pagination.totalCount);
          _this.BonusDialog.totalPage = Number(r.Data.Pagination.totalPage);
          $(".el-table__body-wrapper").eq(1).scrollTop(0);
        } else {
          _this.$message({
            message: r.Message,
            type: "error"
          });
        }
      });
    },
    gotoProduct: function gotoProduct(id) {
      this.$router.push({
        name: "allocatedProducts",
        params: {
          vendorId: id
        }
      });
    },
    addVendoorChange: function addVendoorChange() {
      this.newAddVendorDialog.ourmallId = "";
      this.newAddVendorDialog.content = "";
    },
    selectClick: function selectClick(obj) {
      var shopifyId = obj.shopifyId,
          name = obj.name;
      this.filterParams.productId = shopifyId;
      this.filterParams.productName = name;
      this.filterGetItem();
    },
    getProductSelectList: function getProductSelectList(query, cb) {
      var _this2 = this;

      if (query !== "") {
        this.$apiCall("api.Product.findByUser", {
          name: query
        }, function (r) {
          if (r.ErrorCode == 9999) {
            // this.productSelect.options = r.Data.Results
            cb(r.Data.Results);
          } else {
            _this2.$elementMessage(r.Message, "error");

            if (r.ErrorCode == "1002" || r.ErrorCode == "1003") {
              _this2.$userFailure();
            }
          }
        });
      }
    },
    goPurchase: function goPurchase(rid, name) {
      this.$router.push({
        path: "/Orders-processing",
        query: {
          rid: rid,
          name: name
        }
      });
    },
    onCopy: function onCopy() {
      this.$elementMessage("Copy success", "success");
    },
    onError: function onError() {
      this.$elementMessage("Copy failed", "error");
    },
    closeInviteDialog: function closeInviteDialog() {
      this.inviteDialog.visible = false;
    },
    openInviteDialog: function openInviteDialog(id) {
      var _this3 = this;

      this.$showLoading();
      this.inviteDialog.inviteCode = "";
      this.inviteDialog.visible = true;
      this.$apiCall("api.Relationship.getInviteContents", {
        relationshipId: id
      }, function (r) {
        if (r.ErrorCode == 9999) {
          var arr = window.location.hostname.split('.');

          if (window.location.host.toLowerCase().indexOf("sandbox") >= 0) {
            _this3.inviteDialog.inviteCode = "http://sandboxvendor.".concat(/^(\w+\.)+[a-zA-Z]+$/.test(window.location.hostname) ? arr[arr.length - 2] : 'ourmall', ".com/#/invite?rid=").concat(r.Data.Results.inviteCode);
          } else {
            _this3.inviteDialog.inviteCode = "https://vendor.".concat(/^(\w+\.)+[a-zA-Z]+$/.test(window.location.hostname) ? arr[arr.length - 2] : 'ourmall', ".com/#/invite?rid=").concat(r.Data.Results.inviteCode);
          }
        } else {
          _this3.$elementMessage(r.Message, "error");

          if (r.ErrorCode == "1002" || r.ErrorCode == "1003") {
            _this3.$userFailure();
          }
        }

        _this3.$hideLoading();
      });
    },
    handleCloseNewEditDialog: function handleCloseNewEditDialog() {
      this.newEditDialog.visible = false;
    },
    handleClosenewAddVendor: function handleClosenewAddVendor() {
      this.newAddVendorDialog.visible = false;
    },
    gotoInvoice: function gotoInvoice(item) {
      this.$router.push({
        name: "WaitingForSourcing",
        params: {
          id: item.id,
          name: item.vendorName
        }
      });
    },
    delVendor: function delVendor(item) {
      var _this4 = this;

      //删除
      this.$confirm("Are you sure you want to delete it?", "", {
        confirmButtonText: "Delete",
        cancelButtonText: "Discard",
        type: "error"
      }).then(function () {
        _this4.$apiCall("api.Relationship.deleteByCustomer", {
          relationshipId: item.id
        }, function (r) {
          if (r.ErrorCode == 9999) {
            _this4.$message({
              message: "Success",
              type: "success"
            });

            _this4.getItem(true);

            _this4.$emit("getNum");
          } else {
            _this4.$elementMessage(r.Message, "error");

            if (r.ErrorCode == "1002" || r.ErrorCode == "1003") {
              _this4.$userFailure();
            }
          }
        });
      }).catch(function () {});
    },
    newEditVendor: function newEditVendor(item) {
      var nnfold = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : true;
      // this.newEditDialog = JSON.parse(this.addVendorDialogDefault);
      this.newEditDialog.isUnfold = nnfold;
      this.newEditDialog.shopUrl = item.shopUrl;
      this.newEditDialog.name = item.vendorName;
      this.newEditDialog.id = item.id;
      this.newEditDialog.vendorId = item.vendorId;
      this.newEditDialog.shopName = item.shopName;
      this.newEditDialog.email = item.vendorEmail;
      this.newEditDialog.remark = item.vendorRemark;
      this.newEditDialog.weChat = item.vendorWeChat;
      this.newEditDialog.skype = item.vendorSkype;
      this.newEditDialog.line = item.vendorLine;
      this.newEditDialog.QQ = item.vendorQQ;
      this.newEditDialog.wangwang = item.vendorWangwang;
      this.newEditDialog.whatsapp = item.vendorWhatsapp;
      this.newEditDialog.loading = false;
      this.newEditDialog.visible = true;
    },
    newSaveVendor: function newSaveVendor() {
      var _this5 = this;

      if (!this.newEditDialog.name) {
        this.$elementMessage("Customer name required", "error");
        return;
      }

      this.newEditDialog.loading = true; // this.$Burying()

      var url = "api.Relationship.addByCustomer";
      var params = {
        name: this.newEditDialog.name,
        email: this.newEditDialog.email,
        remark: this.newEditDialog.remark,
        weChat: this.newEditDialog.weChat,
        skype: this.newEditDialog.skype,
        line: this.newEditDialog.line,
        QQ: this.newEditDialog.QQ,
        wangwang: this.newEditDialog.wangwang,
        whatsapp: this.newEditDialog.whatsapp,
        shopUrl: this.newEditDialog.shopUrl
      };

      if (this.newEditDialog.id) {
        params.relationshipId = this.newEditDialog.id;
        url = "api.Relationship.changeByCustomer";
      }

      this.$apiCall(url, params, function (r) {
        _this5.addVendorDialog.loading = false;

        if (r.ErrorCode == 9999) {
          _this5.newEditDialog.visible = false;

          _this5.$elementMessage(_this5.newEditDialog.id ? "customer successfully edited" : "customer successfully added", "success");

          _this5.getItem(true);
        } else {
          _this5.$elementMessage(r.Message, "error");

          if (r.ErrorCode == "1002" || r.ErrorCode == "1003") {
            _this5.$userFailure();
          }
        }

        _this5.newEditDialog.loading = false;
      });
    },
    openAddVendor: function openAddVendor() {
      // this.addVendorDialog = JSON.parse(this.addVendorDialogDefault);
      // this.addVendorDialog.isShow = true;
      this.newAddVendorDialog.content = "";
      this.newAddVendorDialog.vendorId = "";
      this.newAddVendorDialog.radio = 1;
      this.newAddVendorDialog.btnNextLoading = false;
      this.newAddVendorDialog.name = "";
      this.newAddVendorDialog.visible = true;
    },
    getShopUrl: function getShopUrl() {
      var _this6 = this;

      // if (this.newAddVendorDialog.content.trim() === '' && ) {
      //   this.$elementMessage("Vendor's AliExpress shop / product URL", "error");
      //   return
      // }
      var params = {};

      if (this.newAddVendorDialog.radio === 1) {
        params.name = this.newAddVendorDialog.name;
      } else if (this.newAddVendorDialog.radio === 2) {
        params.url = this.newAddVendorDialog.content;
      } else {
        params.ourmallId = this.newAddVendorDialog.ourmallId;
      }

      this.newAddVendorDialog.btnNextLoading = true; // this.$Burying()

      this.$apiCall("api.Relationship.addUrlByCustomer", params, function (r) {
        if (r.ErrorCode == 9999) {
          _this6.newAddVendorDialog.visible = false;

          _this6.newEditVendor({
            shopUrl: r.Data.Results.shopUrl,
            id: r.Data.Results.id,
            vendorName: r.Data.Results.vendorName,
            shopName: r.Data.Results.shopName,
            vendorId: r.Data.Results.vendorId
          }, false);

          _this6.getItem(true);

          _this6.$emit("getNum");
        } else {
          _this6.$elementMessage(r.Message, "error");

          if (r.ErrorCode == "1002" || r.ErrorCode == "1003") {
            _this6.$userFailure();
          }
        }

        _this6.newAddVendorDialog.btnNextLoading = false;
      });
    },
    addVendorSaveSuccess: function addVendorSaveSuccess() {
      this.getItem(true);
    },
    clearFilter: function clearFilter() {
      //清空筛选
      this.filterParams = JSON.parse(this.filterParamsDefault);

      if (this.$route.query.page == 1) {
        this.getItem();
      } else {
        this.$router.push({
          query: {
            page: 1
          }
        });
      }
    },
    filterGetItem: function filterGetItem() {
      //筛选
      // this.$Burying()
      if (this.$route.query.page == 1) {
        this.getItem();
      } else {
        this.$router.push({
          query: {
            page: 1
          }
        });
      }
    },
    getItem: function getItem(s) {
      var _this7 = this;

      this.$showLoading();
      this.$apiCall("api.Relationship.findByCustomer", {
        page: this.page,
        rowsPerPage: this.rowsPerPage,
        vendorId: this.filterParams.vendorId,
        vendorLike: this.filterParams.vendorLike,
        isRegister: this.filterParams.isRegister,
        shopifyProductId: this.filterParams.productId
      }, function (r) {
        if (r.ErrorCode == "9999") {
          _this7.items = r.Data.Results;
          _this7.total = Number(r.Data.Pagination.totalCount);
          _this7.totalPage = Number(r.Data.Pagination.totalPage); // this.$emit("getTableHeight", this);

          if (!s) _this7.$root.$children[0].$refs.mainScroll.wrap.scrollTop = 0;
        } else {
          _this7.$elementMessage(r.Message, "error");

          if (r.ErrorCode == "1002" || r.ErrorCode == "1003") {
            _this7.$userFailure();
          }
        }

        _this7.$hideLoading();
      });
    },
    toPage: function toPage(val) {
      if (val != this.$route.query.page) this.$router.push({
        query: {
          page: val
        }
      });
    },
    changePageSize: function changePageSize(val) {
      this.rowsPerPage = val;
      localStorage.setItem("c_myVendorPerPage", val);
      this.getItem();
    },
    gotoPage: function gotoPage() {
      this.page = this.$route.query.page ? this.$route.query.page : 1;
      this.getItem();
    }
  }
};